.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.selectRow:hover {
  cursor: pointer;
  box-shadow: inset 0px 0px 5px 0px #000;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.card__item {
  padding: 15px;
  width: 200px;
  text-align: left;
  border-bottom: 1px solid black;
}
.card__item:last-child {
  border-bottom: none;
}
.header {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-bottom: 10px;
  padding-top: 10px;
  background-color: #303f9f;
  position: fixed;
  top: 0;
  z-index: 999;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
}
.headerInner {
  width: 90%;
  max-width: 1000px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}
.input {
  padding-bottom: 15px !important;
}
.signIn {
  padding-top: 10px !important;
}
.sign__form {
  display: grid;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main__inner {
  min-height: -webkit-fill-available;
  display: flex;
  justify-content: center;
  align-items: center;
}

.root__inner {
  padding-top: 100px;
}

.main__tableDiv {
  min-width: 100%;
  display: flex;
  justify-content: center;
  padding-bottom: 50px;
}
.tableDiv {
  display: flex;
  flex-direction: column;

  max-width: 100%;
}
.table {
  width: 100%;
}
.inputFilter {
  align-self: flex-start;
}
.main__addressDIv {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr;
}
.address__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: .2;
  transition: 0.7s;
  pointer-events: none;
}
.address__card {
  display: flex;
  align-items: center;
  justify-content: center;
}

.edit__form {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
}
.edit__button {
  position: absolute !important;
  z-index: 1;
  transition: bottom .9s, right .4s!important;
  bottom: 50%;
  right: 50%;
}

.edit__button.hidden {
  bottom: 3%;
  right: 3%;
  align-self: flex-end;
}


@media (max-width: 550px) {
  .main__addressDIv {
    grid-template-columns: 1fr;
  }
}
